.exercise-form-container-strength{
    font-family: 'Comfortaa', cursive;
}
.add-more-fields{
    margin-bottom: 10px;
    font-family: 'Comfortaa', cursive;
    background-color: rgb(133, 171, 169);
    font-size: 15px;
    border-radius: 10px;
    height: 25px;
    width: 200px;
}
.add-more-fields:hover{
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    background-color: rgb(24, 140, 189);
}
.duplicated-fields{
    margin-bottom: 10px;
    margin-top: 10px;
}


