.rbc-date-cell:hover, .rbc-date-cell:focus, .rbc-date-cell:active {
    color: #99C9ED;
    text-decoration: none;
}
.rbc-event-content:hover{
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}
.rbc-event-content{
    text-align: center;
}

.rbc-date-cell {
    font-family: Lato, 'Helvetica Neue', Helvetica, sans-serif;
    font-weight: lighter;
    text-decoration: none;

    -webkit-transition: color 0.1s, background-color 0.1s;

    -o-transition: color 0.1s, background-color 0.1s;

    transition: color 0.1s, background-color 0.1s;
}
.rbc-date-cell {
    position: relative;
    display: block;
    padding: 16px 0;
    margin: 0 12px;
    letter-spacing: 1px;
    font-size: 12px;
    line-height: 16px;
    font-weight: 600;
    text-transform: uppercase;
    -webkit-transition: color 0.1s,background-color 0.1s,padding 0.2s ease-in;
    -o-transition: color 0.1s,background-color 0.1s,padding 0.2s ease-in;
    transition: color 0.1s,background-color 0.1s,padding 0.2s ease-in;
    color: #99C9ED;
}
.rbc-date-cell::before {
    content: '';
    display: block;
    position: absolute;
    bottom: 3px;
    left: 0;
    height: 3px;
    width: 100%;
    background-color: #99C9ED;
    -webkit-transform-origin: right top;
        -ms-transform-origin: right top;
            transform-origin: right top;
    -webkit-transform: scale(0, 1);
        -ms-transform: scale(0, 1);
            transform: scale(0, 1);
    -webkit-transition: color 0.1s,-webkit-transform 0.2s ease-out;
    transition: color 0.1s,-webkit-transform 0.2s ease-out;
    -o-transition: color 0.1s,transform 0.2s ease-out;
    transition: color 0.1s,transform 0.2s ease-out;
    transition: color 0.1s,transform 0.2s ease-out,-webkit-transform 0.2s ease-out;
}

.rbc-date-cell:active::before {
    background-color: #99C9ED;
}

.rbc-date-cell:hover::before, .rbc-date-cell:focus::before {
    -webkit-transform-origin: left top;
        -ms-transform-origin: left top;
            transform-origin: left top;
    -webkit-transform: scale(1, 1);
        -ms-transform: scale(1, 1);
            transform: scale(1, 1);
    background-color: #292B2F;
}

.rbc-toolbar-label{
    color: rgb(39, 35, 92);
    font-size: 25px;
    
}
.rbc-date-cell {
    color: black;
    font-size: 18px;
}
div.popup-overlay > div{
    font-size: 18px;
    width: 300px !important;
}


